<template>
  <ve-histogram
    :data="data"
    :grid="grid"
    :extend="chartExtend"
    :settings='settings'
    height="250px"
    width="99%"
    :legend-visible="legend_Visible"
    :legend-position="legend_Position"
  ></ve-histogram>
</template>

<script>
export default {
  data() {
    this.grid = {
      left: 10,
      bottom: 5,
      top: 30
    };
    return {
      chartExtend: {
        series: {
          lineStyle: {
            type: "dotted", // 折线为虚线
            width: 1 //折线宽度
          },
          align: "center",
          symbol: "circle", //标记点 实心
          symbolSize: 10 // 标记点大小
        },
        xAxis: {
          splitLine: {
            show: false // 统计图分割线 x轴
          },
          axisLine: {
            show: false
          }
        },
        yAxis: {
          splitLine: {
            show: true,
            lineStyle: {
              color: "#7F8184",
              type: "dotted",
              width: 1
            }
          },
          position: "left",
          axisLine: {
            show: false,
            lineStyle: {
              color: "#7F8184"
            }
          }
        }
      }
    };
  },
  props: ['data','legend_Visible','legend_Position','settings']
};
</script>

