<template>
  <div class="content">
    <section class="top">
      <div class="topContent">
        <div class="chuck" v-for="(item,index) of topItem" :key="index">
          <div class="icoDiv" :style="{'background':item.bgColor}">
            <van-icon name="gem" color="#fff" size="20" />
          </div>
          <p>{{item.name}}</p>
          <p>{{item.num}}人</p>
        </div>
      </div>
    </section>
    <section class="vipSex">
      <div class="vipContent">
        <div class="title">会员性别</div>
        <div class="vipLine">
          <div class="chuck" v-for="(item,index) of vipJson" :key="index">
            <img :src="item.ico" />
            <p>{{item.sex == 1 ? '男':'女'}}：</p>
            <p>{{item.number}}人</p>
          </div>
        </div>
      </div>
    </section>
    <section class="charts">
      <div class="title">
        <p>会员走势图</p>
        <div class="selectBtn">
          <div :class='["btn",selectActive === 1 ? "active":""]' @click="selectDay(1)">最近30天</div>
          <div :class='["btn",selectActive === 2 ? "active":""]' @click="selectDay(2)">月度走势</div>
        </div>
      </div>
      <veHistogram
        :data="this.getRespone.json"
        :legend_Visible="false"
        :settings="historgramSetting"
      />
    </section>
  </div>
</template>

<script>
import veHistogram from "@/components/veHistogram";
export default {
  data() {
    this.historgramSetting = {
      yAxisName: [""],
      itemStyle:{
        color: '#FF8A60',
        borderColor: "#FF8A60"
      }
    };
    return {
      topItem: [
        { name: "普通会员", bgColor: "#DAD6CC", num: "200" },
        { name: "金卡会员", bgColor: "#D7B761", num: "80" },
        { name: "钻石会员", bgColor: "#4192E5", num: "40" }
      ],
      vipJson: [
        {
          ico: require("@/assets/b.png"),
          sex: "1",
          number: "180",
          color: "#DCF5FF"
        },
        {
          ico: require("@/assets/g.png"),
          sex: "2",
          number: "140",
          color: "#FFDADA"
        }
      ],
      selectActive: 1
    };
  },
  methods: {
    selectDay(id) {
      this.selectActive = id;
    }
  },
  components:{
    veHistogram
  }
};
</script>

<style lang="scss" scoped>
.topContent {
  @include fr(center);
  padding: 0.8rem;
  .chuck {
    @include fc(center);
    flex: 1;
    .icoDiv {
      @include wh(2.1rem, 2.1rem);
      @include fc(center);
      border-radius: 0.2rem;
      margin-bottom: 0.3rem;
    }
    p {
      @include sc(0.8rem, $Tline);
    }
    p:last-child {
      @include sc(0.9rem, $fColor);
      font-weight: bold;
      margin-top: 0.1rem;
    }
  }
  .chuck:nth-child(2) {
    border-left: 0.04rem solid $F5;
    border-right: 0.04rem solid $F5;
  }
}
.title {
  padding: 0.5rem;
  background: $DD;
  @include sc(0.9rem, $D6);
  @include fr(space-between);
}
.vipLine {
  @include fr(center);
  .chuck {
    @include fr(center);
    padding: 1rem 0.5rem;
    flex: 1;
    img {
      @include wh(2rem, 2rem);
      margin-right: 0.8rem;
    }
  }
  .chuck:last-child {
    border-left: 0.05rem solid $F5;
  }
}
.charts {
  .selectBtn {
    @include fr(center);
    .btn {
      border: 0.05rem solid $vip;
      padding: 0.2rem;
      @include wh(5.5rem, auto);
      @include sc(0.8rem, $D6);
      background-color: $fc;
    }
    .active {
      color: $fc;
      background-color: $vip;
    }
  }
}
</style>